import { BrowserModule } from '@angular/platform-browser';
import { ErrorHandler, NgModule } from '@angular/core';
import { IonicApp, IonicErrorHandler, IonicModule } from 'ionic-angular';
import { SplashScreen } from '@ionic-native/splash-screen';
import { StatusBar } from '@ionic-native/status-bar';

import { MyApp } from './app.component';
import { HomePage } from '../pages/home/home';
import { VirtualizacaoPage } from '../pages/virtualizacao/virtualizacao';
import { ProcessoPage } from '../pages/processo/processo';
import { SobrePage } from '../pages/sobre/sobre';
import { ComunicacaoPage } from '../pages/comunicacao/comunicacao';
import { ArmazenamentoPage } from '../pages/armazenamento/armazenamento';

@NgModule({
  declarations: [
    ArmazenamentoPage,
    ComunicacaoPage,
    MyApp,
    HomePage,
    ProcessoPage,
    SobrePage,
    VirtualizacaoPage,
  ],
  imports: [
    BrowserModule,
    IonicModule.forRoot(MyApp)
  ],
  bootstrap: [IonicApp],
  entryComponents: [

    ArmazenamentoPage,
    ComunicacaoPage,
    MyApp,
    HomePage,
    ProcessoPage,
    SobrePage,
    VirtualizacaoPage,
  ],
  providers: [
    StatusBar,
    SplashScreen,
    {provide: ErrorHandler, useClass: IonicErrorHandler}
  ]
})
export class AppModule {}



